const StoryList = ({ stories }) => (
  <div>
    {stories.map(story => (
      <div key={story.id} className="story">
        <h2 className="story-title">
          <a href={story.url}>{story.title}</a>
        </h2>
        <div className="story-detail">
          <span>{story.points || 0}</span>
        </div>
      </div>
    ))}
  </div>
);
export default StoryList;
